import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            info: '',
			i:[],
			time:null
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$get('/InspectionAPI/Censorate/CensorateRecordAndDetail',{
				CensorateRecordId:this.$route.query.CensorateRecordId
			}).then(o => {
				this.info = o
				this.i = o.Details
				this.time = o.PayOrderTime.split(" ")[0]
			}) 
        }
    }
}



